<template>
  <div class="login">
    <h2 class="title">My Blog</h2>
    <form class="form">
      <span>用户：</span><input v-model="loginForm.username" type="text" name="username"/><br>
      <span>密码：</span><input v-model="loginForm.password" type="password" name="password"/><br>

      <!--<button @click.prevent="submit()">GO，去后台</button>-->
      <div class="submit" ><a class="submit_a" href="javascript:;" @click="submit">登录</a></div>
    </form>
  </div>
</template>

<script type="text/ecmascript-6">
  import axios from "axios";

  export default{
    name: 'login',

    data() {
      return {
        loginForm: {
          username: '',
          password: '',
        }
      };
    },

    methods: {
      submit() {
        const that = this;
        axios.post('/api/login', this.loginForm)
          .then(function (res) {
            if (res.data.code === 200) {
              alert(res.data.msg);
              that.$router.push('/admin');
            } else {
              alert(res.data.msg);
            }
          })
          .catch(function (error) {
            console.log(error);
          });
      }
    }
  }


</script>

<style lang="stylus" type="text/stylus" rel="stylesheet/stylus">
  .login
    background-color: #B0DEDB
    position: absolute
    height: 100%
    width: 100%
    .title
      color: #38817A
      margin: 200px 50px auto
      padding-left :30px
    .form
      margin: 100px auto
      span
        color:#028090
      input
        margin-top 10px
        width: 300px
        height: 30px
      .submit
        margin-top 30px
        margin-left :25px
        font-size: 20px
        .submit_a
          text-decoration:none
          color:#028090


</style>



