<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <form>
      用户名：<input type="text" name="username" v-model="userName"><br>
      密码：<input type="text" name="age" v-model="age"><br>
      <a href="javascript:;" @click="addUser">提交</a>
    </form>
  </div>
</template>
<script>
  export default {
    name: 'hello',
    data () {
      return {
        msg: '欢迎我的博客',
        userName: '',
        age: ''
      }
    },
    methods: {
      addUser() {
        var name = this.userName;
        var age = this.age;
        this.$http.post('/api/user/addUser', {
          username: name,
          age: age
        },{}).then((response) => {
          console.log(response);
        })
      }
    }
  }
</script>

<style lang="stylus" type="text/stylus" rel="stylesheet/stylus">
  .hello
    background-color :white
    margin :20px auto
    width :800px
    height :300px

</style>



