<template>
  <el-form :model="SubmitForm" ref="ruleForm" label-width="100px" class="demo-submitForm">
    <el-form-item label="标题" prop="title" style="width:500px">
      <el-input v-model="SubmitForm.title" ></el-input>
    </el-form-item>
    <el-form-item label="分类" prop="category" style="width:500px">
      <el-input v-model="SubmitForm.category"></el-input>
    </el-form-item>
    <el-form-item label="简介" prop="description" style="width:500px">
      <el-input type="textarea" resize="none" :rows="5" v-model="SubmitForm.description"></el-input>
    </el-form-item>
    <el-form-item label="内容" prop="content">
      <el-input type="textarea" resize="none" :rows="15" v-model="SubmitForm.content"></el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="submitart">提交</el-button>
      <el-button type="primary" @click="resetForm('ruleForm')">重置</el-button>
    </el-form-item>
  </el-form>
</template>

<script type="text/ecmascript-6">
  import axios from "axios";

  export default {
    data() {
      return {
        SubmitForm: {
          title: '',
          category: '',
          description:'',
          content:''
        },
      };
    },
    methods: {
      submitart() {
        this.$confirm('是否提交', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
        }).then(() => {
          this.submit();
          this.$message({
            type: 'success',
            message: '提交成功!'
          });
          this.$router.push('/admin/manage');
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消'
          });
        });
      },
      submit() {
        axios.post('/api/article', this.SubmitForm)
          .then(function(res){})
          .catch(function (error) {
            console.log(error);
          });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    }
  }
</script>

<style lang="stylus" type="text/stylus" rel="stylesheet/stylus">

</style>



