<template>
  <div class="table">
    <el-table :data="datas" align="left" border max-height="650" style="width: 100%" >
      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column prop="ID" label="ID" width="100"></el-table-column>
      <el-table-column prop="title" label="标题"  width="250"></el-table-column>
      <el-table-column prop="category" label="分类" width="200"></el-table-column>
      <el-table-column prop="description" label="简介" width="700"></el-table-column>
      <el-table-column label="操作" width="300">
        <template slot-scope="scope">
          <el-button size="mini" @click="edit(scope.row)">编辑</el-button>
          <el-button size="mini" @click="open(scope.row)" type="danger">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="edit">
      <el-dialog width="70%" title="内容编辑" :visible.sync="dialogFormVisible">
        <el-form :model="form" label-width="100px">
          <el-form-item label="标题" style="width:500px">
            <el-input v-model="form.title"></el-input>
          </el-form-item>
          <el-form-item label="分类" style="width:500px">
            <el-input v-model="form.category"></el-input>
          </el-form-item>
          <el-form-item label="简介" style="width:500px">
            <el-input type="textarea" resize="none" :rows="5" v-model="form.description"></el-input>
          </el-form-item>
          <el-form-item label="内容">
            <el-input type="textarea" resize="none" :rows="15" v-model="form.content"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="decide_edit">确 定</el-button>
        </div>
      </el-dialog>
    </div>
    <div class="pagination">
      <el-pagination
        @current-change ="handleCurrentChange"
        layout="prev, pager, next"
        :total="1000">
      </el-pagination>
    </div>
  </div>
</template>


<script type="text/ecmascript-6">
  import axios from "axios";

  export default {
    data() {
      return {
        datas: [],
        dialogTableVisible: false,
        dialogFormVisible: false,
        form: {
          mod_id: '',
          title: '',
          category: '',
          description: '',
          content: '',
          delivery: false
        },
        cur_page: 1,
        count:'',
      }
    },

    methods: {
      getdata() {
        const that = this;
        axios.post('/api/article-table',{page:that.cur_page})           //重新获取数据刷新表格
          .then(function (res) {
            that.datas = res.data.data;
            let leng=res.data.data.length;
            console.log(leng);
          })
          .catch(function (error) {
            console.log(error);
          });
      },

      handleDelete(row) {          //删除函数
        const that = this;
        axios.post('/api/article-table-handle', {id: row.ID, act: 'del'}).then(function (res) {
          console.log(res.data.code);
          if (res.data.code == 200) {
            that.$message.success('删除成功');
            that.getdata();         //重新获取数据刷新表格
          } else {
            this.$message.error('删除失败');
          }
        }).catch(function (error) {
          console.log(error);
        });
      },
      open(row) {        //删除弹框打开确认函数
        this.$confirm('此操作将永久删除该文件, 是否继续?', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
        }).then(() => {
          this.handleDelete(row);           //调用删除函数
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
      edit(row) {             //点击编辑  呈现要编辑的那条的全部内容
        const that = this;
        axios.post('/api/article-table-handle', {id: row.ID, act: 'mod'}).then(function (res) {
          if (res.data.code == 200) {
            that.form.mod_id = res.data.mod_data.ID;
            that.form.title = res.data.mod_data.title;
            that.form.category = res.data.mod_data.category;
            that.form.description = res.data.mod_data.description;
            that.form.content = res.data.mod_data.content;
            that.dialogFormVisible = true
          } else {
            this.$message.error('编辑失败');
          }
        }).catch(function (error) {
          console.log(error);
        });
      },
      decide_edit() {              //确认编辑  进行数据库修改函数
        const that = this;
        this.dialogFormVisible = false;
        axios.post('/api/article', this.form)
          .then(function (res) {
            that.$message.success('编辑成功');
            if (res.data.code == 200) {
             that.getdata();     //重新获取数据刷新表格
            }
          })
          .catch(function (error) {
            console.log(error);
          });
      },

      handleCurrentChange(val) {
        this.cur_page = val;
        console.log(this.cur_page);
        const that = this;
        axios.post('/api/article-table',{page:that.cur_page})           //重新获取数据刷新表格
          .then(function (res) {
            that.datas = res.data.data;

          })
          .catch(function (error) {
            console.log(error);
          });
      },
    },
    created() {           //初始进入加表格载数据
      axios.get('/api/article-count')           //重新获取数据刷新表格
        .then(function (res) {
            this.count=res.data.count;
            console.log(this.count);
        })
        .catch(function (error) {
          console.log(error);
        });

      this.getdata()
    },
  }
</script>

<style lang="stylus" type="text/stylus" rel="stylesheet/stylus">

  .table
    overflow-x: hidden
    overflow-y: hidden
  .pagination
    margin: 20px 0
    text-align: right

</style>



